// 
.switch-card {
    margin: 30px 0;

    a {
        text-decoration: none;
        text-align: center;
        justify-content: center;
        transition: all .5s;

        &:hover {
            color: #FFF;
            background: $blue;
        }

        &:active {
            color: #FFF;
            background: $main;
        }
    }

    span {
        background-color: $third;
        text-align: center;
        justify-content: center;
    }

    .card {
        height: 130px;
    }

    h4 {
        margin: 0;
    }
}